* {
  box-sizing: border-box;
}

body {
  color: #060b35;
  margin: 0;
}

.main {
  margin: 0 auto;
}

.header {
  background-color: #fff;
  width: 100%;
  box-shadow: 0 2px 4px #0000000d;
}

.header__content {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.logo {
  align-self: center;
  line-height: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__content {
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
  list-style-type: none;
}

.nav__content:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  color: #00acdc;
  background-color: #00acdc;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.stars {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

.catalog {
  grid-gap: 46px 48px;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.product-card {
  box-sizing: border-box;
  color: #060b35;
  border: 1px solid #f3f3f3;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
  transition: all .3s;
}

.product-card__image {
  box-sizing: border-box;
  background-size: cover;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__name {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.product-card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__code:before {
  content: "Product code: ";
}

.product-card__score {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 23px;
  display: flex;
}

.product-card__reviews-counter {
  font-size: 10px;
}

.product-card__price {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price-text {
  color: #616070;
}

.product-card__price-value {
  font-size: 16px;
  font-weight: 700;
}

.product-card__price-value:before {
  content: "$";
}

.product-card__buy-btn {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.product-card__buy-btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.product-card:hover {
  scale: 120%;
}

.product-card:hover .product-card__name {
  color: #34568b;
}
